<template>
    <h1>拉鍊損壞計數器</h1>
    <p>損壞次數：<span id="damageCounter">0</span></p>
</template>

<style lang="scss">
body {
    font-family: 'Arial', sans-serif;
    text-align: center;
}

h1 {
    color: #3498db;
}

p {
    font-size: 18px;
    color: #2ecc71;
}
</style>

<script>
// 檔案在本地端的路徑
const filePath = "./zipper_python/file.json";
let number;
let numberElement = document.getElementById('damageCounter');

// 使用 fetch 發送本地端檔案的請求
fetch(filePath)
    .then(response => {
        if (!response.ok) {
            throw new Error(`無法抓取檔案 ${filePath}`);
        }
        return response.json();
    })
    .then(data => {
        console.log(`成功抓取檔案 ${filePath}：`, data.num);
        number = data.num;
        numberElement.textContent = number;
    })
    .catch(error => {
        console.error('抓取檔案時發生錯誤：', error.message);
    });

</script>
